// 
export const getCardCount = (cardType: string) => {
    const gameConfig = read();
    let cardCount = 0;

    for (const tempCard of gameConfig.deck[cardType]) {
        if (tempCard.count != 0) {
            cardCount += 1;
        }
    }

    return cardCount;
}

// 
export const getDeckListByCount = (deckType: string) => {
    const gameConfig = read();
    const deckList = [];

    for (let i = 0; i < gameConfig.deck[deckType].length; i++) {
        const card = gameConfig.deck[deckType][i];
        deckList.push(card);
    }

    return deckList;
}

//  
export const specialCard = (card : any) => {
    const gameConfig = read();

    // 
    // ---------------------------------

    // 
    if (card.name == "Capitalism") {
        deleteCard(card);

        ElMessage({
            message: '+2-2513624',
            grouping: true,
            showClose: true,
            duration: 0
        });
    }

    // 
    if (card.name == "Make-Wealth") {
        deleteCard(card);

        ElMessage({
            message: ' +6',
            grouping: true,
            showClose: true,
            duration: 0
        });

        const gameConfig = read();
        gameConfig.money += 6;
        playerMoney.value = gameConfig.money;
        save(gameConfig);
    }

    // 
    if (card.name == "Change-Card") {
        deleteCard(card);
        const gameConfig = read();

        let cardNum = 0;
        let drawsCount = 0;

        for (let i = 0; i < deckType.length; i++) {

            const deckList = gameConfig.deckList[deckType[i]];

            for (let j = 0; j < deckList.length; j++) {
                deleteCard(deckList[j]);
                cardNum += 1;
                drawsCount += 1;
            }

        }

        gameConfig.drawCount += drawsCount;
        save(gameConfig);

        drawCardCount.value = gameConfig.drawCount;
        ElMessage({
            message: " " + cardNum + "   " + drawsCount + " ",
            grouping: true,
            showClose: true,
            duration: 0
        });
    }

    // 
    if (card.name == "Devils-Pact") {
        deleteCard(card);
        const gameConfig = read();
        gameConfig.devilspact = 2;
        save(gameConfig);

        ElMessage({
            message: "",
            grouping: true,
            showClose: true,
            duration: 0
        });
    }

    // 
    if (card.name == "Tribute") {
        deleteCard(card);

        ElMessage({
            message: "112",
            grouping: true,
            showClose: true,
            duration: 0
        });
    }

    // 
    if (card.name == "Duel") {
        deleteCard(card);

        do {
            players = shuffle(players).value;
        } while (players[0] == gameConfig.roleId);

        ElMessage({
            message: "" + players[0] + "" +
                                                    "",
            grouping: true,
            showClose: true,
            duration: 0
        });
    }

    // 
    if (card.name == "Equivalent-Exchange") {
        deleteCard(card);

        do {
            players = shuffle(players).value;
        } while (players[0] == gameConfig.roleId);

        ElMessage({
            message: " " + players[0] + " ",
            grouping: true,
            showClose: true,
            duration: 0
        });
    }

    // 
    if (card.name == "Blessed-To-Share") {
        deleteCard(card);

        do {
            players = shuffle(players).value;
        } while (players[0] == gameConfig.roleId);

        ElMessage({
            message: " " + players[0] + " ",
            grouping: true,
            showClose: true,
            duration: 0
        });
    }

    // 
    if (card.name == "Share-The-Difficulties") {
        deleteCard(card);

        do {
            players = shuffle(players).value;
        } while (players[0] == gameConfig.roleId);

        ElMessage({
            message: " " + players[0] + " ",
            grouping: true,
            showClose: true,
            duration: 0
        });
    }

    // 

    // 

    // 
    if (card.name == "Lost-Wallet") {
        deleteCard(card);

        const gameConfig = read();
        gameConfig.money = 0;
        save(gameConfig);

        playerMoney.value = gameConfig.money;
        ElMessage({
            message: "~~",
            grouping: true,
            showClose: true,
            duration: 0
        });
    }
}
